@import url(https://fonts.googleapis.com/css?family=Lobster);
@import url(https://fonts.googleapis.com/css?family=Staatliches);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display+SC);
body {
    padding:0;
    margin:0;
    font-family: 'Roboto', sans-serif;
}

.font_righteous {
    font-family: 'Righteous', cursive;
}

.bck_black {
    background: #3c3c3c;
}
.bck_white {
    background: #ffffff;
}
.bck_red {
    background: #ff4800;
}
.bck_yellow {
    background: #ffa800;
}

.iconImage {
    width: 2vh;
    margin-right: 1vh;
}

.center_wrapper {
    width: 60vw;
    margin:0 auto;
}


/*====================
        HEADER
======================*/


header {
    padding: 10px 0px;
    -webkit-transition: all 300ms ease-in;
    transition: all 300ms ease-in;
}

header .header_logo {
    flex-grow: 1;
}
header .header_logo_venue {
    font-size: 40px;
}

header .header_logo_title {
    text-transform: uppercase;
}

/*====================
        FOOTER
======================*/

footer {
    padding:50px 0px;
    text-align: center;
    font-size: 6vh;
    color:#ffffff;
    background: red;
}

footer .footer_copyright {
    font-size: 2vh;
}

/*====================
        CARROUSEL
======================*/


.carrousel_wrapper {
    height: auto;
    overflow: hidden;
}

.carrousel_image {
    width: 100%;
    height: 100%;
    opacity: 0.8;
    background-size: cover !important;
}

.artist_name {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60vh;
    height: 22vh;
    background-color: rgba(6, 2, 70, 0.486) !important;
    -webkit-transform: translate(-50%, -50%);  
    transform: translate(-50%, -50%);   
    border: 2px solid rgb(250, 250, 250);
}

.artist_name .wrapper {
    border: 2px solid rgb(255, 255, 255);
    height: 17.8vh;
    margin: 0.3vh;
    text-align: center;
    color: rgb(240, 240, 240);
    font-size: 4vh;
    font-family: 'Lobster', cursive;
    padding: 3vh 6vh 0px 6vh;
    text-transform: uppercase;
}

/*====================
        COUNTDOWN
======================*/

.countdown_wrapper {
    position:absolute;
    bottom: 0px;
    color:#ffffff;
}

.countdown_wrapper .countdown_top {
    background: #ff4800;
    font-size: 3vh;
    padding: 1vh;
    display: inline-block;
    text-transform: uppercase;
}

.countdown_wrapper .countdown_bottom {
    display: flex;
    background: #d93d00;
    padding: 1vh 1.2vh;
}

.countdown_wrapper .countdown_time {
    font-size: 7vh;
    border-right: 2px solid rgb(253, 145, 137);
    margin-right: 1vh;
    padding-right: 7vh;
}
.countdown_wrapper .countdown_item:last-child .countdown_time{
    border:none;
}

.countdown_wrapper .countdown_tag {
    text-transform: uppercase;
    font-size: 4vh;
    padding-left: 7px;
}

/*====================
        Events
======================*/

.vn_wrapper {
    display: flex;
    padding: 17vw 0vw;
}

.vn_item {
    width: 100%;
    color:#ffffff;
    text-align: center;
}
                      
.vn_item .vn_outer {
    width: 24vw;
    border: 2px solid #ffffff;
    margin: auto;
    padding: 0.5vw;
}

.vn_item .vn_inner { 
    border: 2px solid #8f8f8f;
    position: relative;
    padding: 0vh 4vh;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}

.vn_item .vn_inner:hover {
    background: #3e3e3e;
}

.vn_item .vn_icon_square {
    width: 7.2vw;
    height: 7.2vw;
    position: absolute;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    top: -4.2vw;
    left: 8vw;
}

.vn_item .vn_icon {
    width: 3.2vw;
    height: 3.2vw;
    background-size: contain !important;
    background-repeat: no-repeat !important;
    position: absolute;
    top: -2vw;
    left: 10vw;
}

.vn_item .vn_title {
    font-size: 4vh;
    margin: 6vw 0px 1vw 0px;
    border-bottom: 1px solid #5d5d5d;
    padding-bottom: 10px;
}

.vn_item .vn_desc {
    font-size: 3vh;
    font-weight: 300;
    margin-bottom: 90px;
}

/*====================
        HIGHLIGHTS
======================*/


.highlight_wrapper h2 {
    text-align: center;
    text-transform: uppercase;
    color:#2c2c2c;
    font-size: 6vh;
}

.highlight_wrapper .highlight_description {
    line-height: 30px;
    font-size: 3vh;
    font-weight: 300;
    border-top: 1px solid #dddddd;
    border-bottom: 1px solid #dddddd;
    padding: 30px 0px;
}

.discount_wrapper {
    display: flex;
    padding: 8vw 0px;
}

.discount_wrapper .discount_porcentage {
    padding-right: 15vw;
}

.discount_wrapper .discount_porcentage span:nth-child(1) {
    color: #ff4800;
    font-size: 7vw;
    font-weight: 40vw;
    display: block;
    line-height: 7vw;
}

.discount_wrapper .discount_porcentage span:nth-child(2) {
    color: #2c2c2c;
    font-size: 4vh;
    font-weight: 40vw;
    display: block;
}


.discount_wrapper .discount_description h3 {
    font-size: 5vh;
    font-weight: 10vw;
    margin: 0px;
    color: #2c2c2c;
}

.discount_wrapper .discount_description p { 
    line-height: 30px;
    font-size: 3vh;
    font-weight: 30vw;
}

/*====================
        PRICING
======================*/

.pricing_section {
    padding: 5vw 0px;
}


.pricing_section h2 {
    color: #ffffff;
    text-transform: uppercase;
    text-align: center;
    font-size: 7vh;
    margin: 0;
    padding-bottom: 5vh;
}

.pricing_wrapper {
    display: flex;
    flex-wrap: wrap;
}
.pricing_wrapper .pricing_item{
    flex-grow: 1;
    width: 30vh;
    padding: 3vh;
    box-sizing: border-box;
}

.pricing_wrapper .pricing_inner_wrapper {
    border:2px solid #ffa800;
    padding: 3vh 2vh;
}

.pricing_inner_wrapper .pricing_title {
    color: #ffffff;
    text-align: center;
    border-bottom: 1px solid #ffa800;
    padding-bottom: 2vh;
}
.pricing_inner_wrapper .pricing_title span:nth-child(1) {
    font-size: 6vh;
    display: block;
}
.pricing_inner_wrapper .pricing_title span:nth-child(2) {
    text-transform: uppercase;
    font-size: 3vh;
    font-weight: 30vh;
}
.pricing_inner_wrapper .pricing_description {
    color: #b8b8b8;
    font-weight: 300;
    font-size: 3vh;
    text-align: center;
    padding: 3vh 0px;
    min-height: 70px;
}

.pricing_inner_wrapper .pricing_buttons {
     text-align: center;
}


/*====================
        LOCATION    
======================*/

.location_wrapper {
    position:relative;
}

.location_tag {
    position: absolute;
    bottom: 0px;
    width: 100%;
}
.location_tag div{
    background: #2c2c2c;
    color: #ffffff;
    text-transform: uppercase;
    width: 30vh;
    margin: 0 auto;
    font-size: 5vh;
    padding: 15px 20px;
    text-align: center;
}
.customBar {
    position: fixed;
    background-color: rgb(124, 124, 212);
    box-shadow: node;
    padding: 10px 0px;

}

.font_header {
    font-family: 'Staatliches', cursive;
}
.customDrawer{
    font-size: 1.2em;
    font-family: 'Playfair Display SC', serif;
    color: rgba(14, 11, 11, 0.836) !important;

}

.item:hover{
    background-color: rgba(71, 83, 252, 0.685) !important;
    color: #fff; 
}

.colorIcon1{
    color: rgb(0, 0, 255) !important;
}

.colorIcon2{
    color: rgb(255, 38, 0) !important;
}
